import { createStore } from "vuex";

export default createStore({
  state: { notes: [] },
  getters: {
    getPostIts: (state) => state.postIts,
    notes: (state) => state.notes,
  },
  mutations: {
    setNotes(state, notes) {
      state.notes = notes;
    },
    addNote(state, note) {
      state.notes.push(note);
    },
    updateNote(state, updatedNote) {
      state.notes = state.notes.map((note) => {
        if (note.id === updatedNote.id) {
          return updatedNote;
        } else {
          return note;
        }
      });
    },
    deleteNote(state, noteId) {
      state.notes = state.notes.filter((note) => note.id !== noteId);
    },
  },
  actions: {
    async fetchNotes({ commit }) {
      await fetch("https://post-it-7m1i.onrender.com/notes")
        .then((response) => response.json())
        .then((notes) => {
          commit("setNotes", notes.notes);
        });
    },
    addNote({ commit }, note) {
      fetch("https://post-it-7m1i.onrender.com/notes", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(note),
      })
        .then((response) => response.json())
        .then((newNote) => {
          commit("addNote", newNote);
        });
    },
    updateNote({ commit }, updatedNote) {
      fetch("https://post-it-7m1i.onrender.com/notes/" + updatedNote.id, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(updatedNote),
      })
        .then((response) => response.json())
        .then((updatedNote) => {
          commit("updateNote", updatedNote);
        });
    },
    deleteNote({ commit }, noteId) {
      fetch("https://post-it-7m1i.onrender.com/notes/" + noteId, {
        method: "DELETE",
      }).then(() => {
        commit("deleteNote", noteId);
      });
    },
  },
  modules: {},
});
